import React, {createContext, useState} from 'react';

interface Window {
    zIndex: number;
    title: string;
    content: JSX.Element | JSX.Element[];
    width?: number;
    height?: number;
}
  
interface WindowsContext {
    minZIndex : number;
    openWindows: Window[];
    openWindow: (title: string, content: JSX.Element | JSX.Element[], width?: number, height?: number) => void;
    closeWindow: (openWindowIndex: number) => void;
    onWindowClicked: (openWindowIndex: number) => void;
}

export const WindowsContext: React.Context<WindowsContext> = createContext({} as WindowsContext);

const WindowsProvider = (props) => {
    const openWindow =  (title: string, content: JSX.Element | JSX.Element[], width?: number, height?:number) => {
        const openWindows = [...windowState.openWindows];
        const openWindowIndex = openWindows.findIndex((window)=> window.title === title);

        if(openWindowIndex === -1) {
        openWindows.push({
            zIndex:  windowState.minZIndex,
            title,
            content,
            width,
            height
        });

        const newWindowState = {
            ...windowState,
            minZIndex: windowState.minZIndex + 1,
            openWindows
        }

        setWindowState(newWindowState);
        } else {
        onWindowClicked(openWindowIndex);
        }
    }

    const onWindowClicked = (openWindowIndex: number) => {
    const openWindow = windowState.openWindows[openWindowIndex];

    if(openWindow && openWindow.zIndex != windowState.minZIndex) {
        const openWindows = [...windowState.openWindows].map((window, index) => {
        if(index === openWindowIndex) {
            window.zIndex = windowState.minZIndex;
        }else {
            window.zIndex -= 1;
        }

        return window;
        });

        setWindowState({
        ...windowState,
        openWindows
        });
    }
    }

    const closeWindow = (openwindowIndex: number) => {
    const openWindows = [...windowState.openWindows];

    openWindows.splice(openwindowIndex, 1);


    setWindowState({
        ...windowState,
        openWindows
    });
    }

    const [windowState, setWindowState] = useState({
        minZIndex: 10,
        openWindows: [] as Window[]
    });

    return (
        <WindowsContext.Provider value={{...windowState, openWindow, closeWindow, onWindowClicked}}>
            {props.children}
        </WindowsContext.Provider>
    );
}

export default WindowsProvider;