import React from 'react';

import "./ParallaxBackground.scss";

import ParallaxLayer from './ParallaxLayer/ParallaxLayer';

const ParallaxBackground = () => {
    const parallaxLayers: number[] = [];
    const layerContext = require.context('../../assets/images/parallax', true);

    for(let i = 0; i < 7; i++) {
        parallaxLayers.push(i)
    }

    return (
        <div className="noScroll">
            <div className="parallax">
                {parallaxLayers.map((layer, index)=><ParallaxLayer layer={layerContext(`./layer_${layer}.png`)} layerNumber={index} key={index}/>)}
                <div className="parallax__cover"></div>
            </div>
        </div>
    )
};


export default ParallaxBackground;