import React from 'react';

import "./Desktop.scss";

import DesktopIcon from './DesktopIcon/DesktopIcon';
import NoContent from '../WindowContents/NoContent/NoContent';
import BrowserContent from '../WindowContents/BrowserContent/BrowserContent';

import BrowserIcon from '../../assets/images/icons/Browser.svg';

const Desktop = () => {
    const desktopIcons = [
        {
            type: "animated",
            icon: "Folder",
            label: "Projects",
            hoverAnimation: true,
            content: <NoContent />
        },
        {
            type: "animated",
            icon: "Resume",
            label: "Resume",
            autoplay: true,
            loop: false,
            hoverAnimation: false,
            content: <NoContent />
        },
        {
            type: "image",
            icon: BrowserIcon,
            label: "Browser",
            hoverAnimation: false,
            windowWidth: window.innerWidth * 0.6,
            windowHeight: window.innerHeight * 0.8,
            content: <BrowserContent />
        }
    ];

    return (
        <div className="desktop">
            <div className="desktop__icons">
                {desktopIcons.map((icon, index) => <DesktopIcon {...icon} key={index} uniqueIdentifier={index} />)}
            </div>
        </div>
    )
};

export default Desktop;