import React, {useContext} from 'react';

import "./Window.scss";
import Draggable from 'react-draggable';
import { WindowsContext } from '../../contexts/WindowsContext';

interface Props {
    title: string;
    zIndex: number;
    arrIndex: number;
    content: JSX.Element | JSX.Element[];
}

const Window =  (props) => {
    const windowsContext = useContext(WindowsContext);
    const onWindowClicked = () => windowsContext.onWindowClicked(props.arrIndex);
    const minWidth = 550;
    const minHeight = 450;
    const defaultX = window.innerWidth / 2 - ((props.width || minWidth) / 2);
    const defaultY = window.innerHeight / 2 - ((props.height || minHeight) / 2);

    return (
        <Draggable
            handle=".window__controls"
            onStart={onWindowClicked}
            defaultPosition={{x: defaultX , y: defaultY}}
        >
            <div className="window" style={{zIndex: props.zIndex, minWidth, minHeight, width: props.width || minWidth, height: props.height || minHeight}} onClick={onWindowClicked}>
                <div className="window__controls">
                    <div className="window__control window__control--close" onClick={windowsContext.closeWindow.bind(null, props.arrIndex)}>Close</div>
                    <div className="window__title"><span>{props.title}</span></div>
                </div>
                <div className="window__content">
                    {props.content}
                </div>
            </div>
        </Draggable>

    );
}

export default Window;