import React from 'react';

import "./TopBarIcon.scss";

interface Props {
    href: string;
    icon: any;
    alt: string;
    target?: string;
    onHover?: Function;
};

const TopBarIcon = (props: Props) => {
    return (
        <a href={props.href} className={`topBar__icon ${props.alt}`} target={props.target} onMouseEnter={()=> {
            if(typeof props.onHover === "function") {
                props.onHover(props.icon);
            }
        }}>
            <img src={props.icon} alt={props.alt}></img>
        </a>
    );
} 

export default TopBarIcon;