import React, {useEffect, useContext} from 'react';

import "./Worm.scss";

import lottie from 'lottie-web';
import { TimelineMax } from 'gsap';

import {SoundContext} from "../../contexts/SoundContext";

const Worm = () => {
    const soundContext = useContext(SoundContext);

    useEffect(()=>{
        const wormDelay = 30; //default: 30
        const wormContainer =  document.querySelector(`#worm`) as Element;
        const parallaxCover = document.querySelector(".parallax") as Element;
        const wormSpeed = 160; //default: 160
        const wallOffset = "-44px";
        const wormSoundEffect = soundContext.loadSound("wormClick.wav");
        let direction =  1; //1: going down -1: going up

        const timeline = new TimelineMax({
            onComplete: function() {
                (this as TimelineMax).restart();
            }
        });

        loadWormAnimation(wormContainer);

        if(wormContainer && parallaxCover){
            timeline.pause();
            timeline.set(wormContainer, {rotation: 90, left: wallOffset, right: "initil"}).add(()=> direction = 1)
                    .to(wormContainer, wormSpeed, {y: parallaxCover.clientHeight})
                    .set(wormContainer, {rotation: -90, left: "initial", right: wallOffset}).add(()=> direction = -1)
                    .to(wormContainer, wormSpeed, {y: "-50px"})

            playWormWithDelay(timeline, wormDelay);
            
            wormContainer.addEventListener("click", () => {
                soundContext.playSound(wormSoundEffect, 0.3);

                const clickedTimeline = new TimelineMax({
                    onComplete: () => playWormWithDelay(timeline, wormDelay)
                });

                timeline.pause();

                clickedTimeline.set(wormContainer, {rotationY: 180});
                clickedTimeline.to(wormContainer, 1, {y: direction === 1 ? "-50px" : parallaxCover.clientHeight});
                clickedTimeline.set(wormContainer, {rotationY: 0});
            });
        }
    }, []);

    const loadWormAnimation = (wormContainer: Element) => {
        return lottie.loadAnimation({
            container: wormContainer,
            renderer: "svg",
            loop: true,
            autoplay: true,
            path: `/assets/animations/Worm.json`
        });
    }

    const playWormWithDelay = (timeline: TimelineMax, wormDelay: number) => {
        setTimeout(function(){
            timeline.restart();
        }, wormDelay * 1000);
    }

    return (
        <div id="worm"></div>
    );
}

export default Worm;