import React, { useEffect, useContext } from 'react';

import "./DesktopIcon.scss";

import lottie from 'lottie-web';
import { WindowsContext } from '../../../contexts/WindowsContext';

interface Props {
    type: string;
    icon: any;
    label: string;
    uniqueIdentifier: number;
    autoplay?: boolean;
    loop?: boolean;
    hoverAnimation?: boolean;
    content: JSX.Element | JSX.Element[];
    windowHeight?: number;
    windowWidth?: number;
};

const DesktopIcon = (props: Props) => {
    const windowsContext = useContext(WindowsContext);

    useEffect(()=>{
        if(props.type === "animated"){
            loadFolderAnimation();
        }
    }, [])

    const loadFolderAnimation = () => {
        const animationContainer =  document.querySelector(`.${props.icon}__container--${props.uniqueIdentifier}`);
        const animation = lottie.loadAnimation({
            container: animationContainer,
            renderer: "svg",
            loop: props.loop || false,
            autoplay: props.autoplay || false,
            path: `/assets/animations/${props.icon}.json`
        });

        animation.setSpeed(0.5);

        if(animationContainer && props.hoverAnimation){
            animationContainer.addEventListener("mouseenter", () => {
                animation.setDirection(1);
                animation.play();
            });

            animationContainer.addEventListener("mouseleave", () => {
                animation.setDirection(-1);
                animation.play();
            });
        }
    }

    const onIconClicked = () => windowsContext.openWindow(props.label, props.content, props.windowWidth, props.windowHeight);

    if(props.type === "animated"){
        return (
            <div className={`desktop-icon desktop-icon--animated ${props.icon}__container--${props.uniqueIdentifier}`} onClick={onIconClicked}>
                <h4 className="desktop-icon__label">{props.label}</h4>
            </div>
        );
    }else {
        return (
            <div className="desktop-icon" onClick={onIconClicked}>
                <img className="desktop-icon__icon" src={props.icon} alt={props.label} />
                <h4 className="desktop-icon__label">{props.label}</h4>
            </div>
        );
    }
}

export default DesktopIcon;