import React, { useContext, useEffect } from 'react';
import './App.scss';

import {TimelineMax, Expo, TweenMax} from 'gsap';

import TopBar from './components/TopBar/TopBar';
import Desktop from './components/Desktop/Desktop';
import ParallaxBackground from './components/ParallaxBackground/ParallaxBackground';
import Worm from './components/Worm/Worm';

import WindowsContainer from './containers/WindowsContainer/WindowsContainer';

import WindowsProvider from './contexts/WindowsContext';
import EmailProvider from './contexts/EmailContext';
import SoundProvider, {SoundContext} from './contexts/SoundContext';

const isDev = false;

const App = () => {
  const soundContext = useContext(SoundContext);

  useEffect(()=>{
      playInitialLoadAnimation();
      initSounds();
  },[]);

  const playInitialLoadAnimation =  () => {
    const parallaxBackground =  document.querySelector(".parallax") as HTMLElement;
    const topBar = document.querySelector(".topBar") as HTMLElement;
    const loadingIndicator = document.querySelector("#loading") as HTMLElement;

    if(!isDev){
      const loadingTimeline = new TimelineMax();
      loadingTimeline
        .to(loadingIndicator, .6, {autoAlpha: 0}, "+=1.5")
        .set(loadingIndicator, {display: "none"})
        .to(parallaxBackground, 3, {scrollTo: {y: 0, x: 0}})
        .set(parallaxBackground, {pointerEvents: "none"})
        .to(topBar, 1, {y: 0, ease: Expo.easeOut}, "-=.5");
    } else {
      TweenMax.set(loadingIndicator, {display: "none"})
      TweenMax.to(parallaxBackground, .1, {scrollTo: {y: 0, x: 0}});
      TweenMax.to(topBar, .1, {y: 0, ease: Expo.easeOut});
      TweenMax.set(parallaxBackground, {pointerEvents: "none"});
    }

    let scrollingTimeout;

    window.addEventListener("wheel", () => {
      parallaxBackground.style.pointerEvents = "auto";
      window.clearTimeout( scrollingTimeout );

      scrollingTimeout = setTimeout(() => {
        parallaxBackground.style.pointerEvents = "none";
      }, 200);
    })
  }

  const initSounds = () => {
    const clickSound = soundContext.loadSound("click.wav");

    window.addEventListener("click", () => {
      soundContext.playSound(clickSound);
    });
  }

  return (
    <div className="App">
      <WindowsProvider>
      <EmailProvider>
      <SoundProvider>
        <Worm />
        <TopBar />
        <Desktop />
        <WindowsContainer />
      </SoundProvider>
      </EmailProvider>
      </WindowsProvider>
      <ParallaxBackground />
    </div>
  );
}

export default App;
