import React from 'react';

import "./NoContent.scss";
import TumbleWeed from '../../../assets/images/tumbleweed.png';

const NoContent = () => {
    return (
        <div className="noContent">
            <div className="noContent__animation">
                <div className="bounce" style={{backgroundImage: `url(${TumbleWeed}`}}></div>
                <div className="pebble"></div>
                <div className="pebble"></div>
                <div className="pebble"></div>
            </div>
            <div className="noContent__text">
                <h2>Coming Soon!</h2>
            </div>
        </div>
    )
};

export default NoContent;