import React, { createContext } from 'react';

const soundContextValue = {
    isMuted: false,
    loadedSounds: [] as HTMLAudioElement[],
    playingSound: false,
    toggleMuted: () => {
        setTimeout(function() {
            soundContextValue.isMuted = !soundContextValue.isMuted;
        }, 1000);
    },
    loadSound: (soundFileName: string):HTMLAudioElement =>  {
        const sound = new Audio(`/assets/sounds/${soundFileName}`);
        sound.preload = "auto";
        sound.load();
        soundContextValue.loadedSounds.push(sound);

        sound.addEventListener("play", (e) => {
           
        });

        return sound;
    },
    playSound: (soundElement: HTMLAudioElement, volume?: number) => {
        if(!soundContextValue.isMuted && !soundContextValue.playingSound) {
             //Pause all sounds except the currently playing one (to prevent sounds from overlapping)
            soundContextValue.loadedSounds.forEach((audioElement) => {
                if(soundElement.currentSrc != audioElement.currentSrc) {
                    audioElement.pause();
                }
            });

            const sound = soundElement.cloneNode() as HTMLAudioElement;
            sound.volume = volume || 1;
            sound.play();
            soundContextValue.playingSound = true;

            setTimeout(()=>{
                soundContextValue.playingSound = false;
            }, 50)
        }
    }
};

interface SoundContext {
    isMuted: boolean;
    loadedSounds: HTMLAudioElement[];
    playingSound: boolean;
    toggleMuted: () => void;
    loadSound: (soundFileName: string) => HTMLAudioElement;
    playSound: (soundElement: HTMLAudioElement, volume?: number) => void;
}

export const SoundContext: React.Context<SoundContext> = createContext(soundContextValue);

const SoundProvider = (props) => {

    document.addEventListener('play', function(e){
        console.log("here");
        const audios = document.getElementsByTagName('audio');
        for(let i = 0, len = audios.length; i < len;i++){
            if(audios[i] != e.target){
                audios[i].pause();
            }
        }
    }, true);

    return <SoundContext.Provider value={soundContextValue}>{props.children}</SoundContext.Provider>
} 

export default SoundProvider;