import React, {useState, useEffect, useContext} from 'react';
import lottie from 'lottie-web';
import { TweenLite } from 'gsap';

import "./TopBar.scss";

import Email from '../../assets/images/icons/email.svg';
import LinkedIn from '../../assets/images/icons/linkedin.svg';
import GitHub from '../../assets/images/icons/github.svg';

import TopBarIcon from './TopBarIcon/TopBarIcon';
import {SoundContext } from '../../contexts/SoundContext';
import {EmailContext} from '../../contexts/EmailContext';

const TopBar = () => {
    const [time, setTime] = useState();
    const email = useContext(EmailContext);
    const soundContext = useContext(SoundContext);

    const topBarIcons = [
        {
            href: `mailto:${email}`,
            icon: Email,
            alt: "email",
            onHover: () => onEmailIconHover()
        },
        {
            href: "https://www.linkedin.com/in/andrei-koreh-71084b169/",
            target: "_blank",
            icon: LinkedIn,
            alt: "linkedin"
        },

        {
            href: "https://github.com/akoreh/Portfolio",
            target: "_blank",
            icon: GitHub,
            alt: "github"
        }
    ];

    useEffect(()=>{
        updateTime();
        loadTopBarAnimations();
    }, []);

    const onEmailIconHover = () => {
        TweenLite.to(document.querySelector(".topBar__email"), 1, {x: 0, autoAlpha: 1});
    };

    const loadTopBarAnimations = () => {
        const batteryContainer =  document.querySelector(".topBar__battery");
        const batteryAnimation = lottie.loadAnimation({
            container: batteryContainer,
            renderer: "svg",
            loop: false,
            autoplay: false,
            path: "/assets/animations/battery.json"
        });

        const wifiContainer =  document.querySelector(".topBar__wifi");
        const wifiAnimation = lottie.loadAnimation({
            container: wifiContainer,
            renderer: "svg",
            loop: true,
            autoplay: true,
            path: "/assets/animations/WIFI.json"
        });
        
        const soundContainer =  document.querySelector(".topBar__sound") as HTMLElement;
        const soundAnimation = lottie.loadAnimation({
            container: soundContainer,
            renderer: "svg",
            loop: false,
            autoplay: false,
            path: "/assets/animations/Sound.json"
        });

        if(soundContext.isMuted) {
            soundAnimation.play();
        }

        batteryAnimation.setSpeed(0.5);
        wifiAnimation.setSpeed(0.2);
        soundAnimation.setSpeed(0.7);

        setTimeout(()=>{
            batteryAnimation.play();

            setInterval(()=>{
                batteryAnimation.goToAndPlay(1,true);
            }, 30000);
        }, 5000);

        soundContainer.addEventListener("click", onToggleSound.bind(null, soundAnimation));
    }

    const onToggleSound = (soundAnimation) => {
        soundAnimation.setDirection(soundContext.isMuted ? -1 : 1);
        soundAnimation.play();
        soundContext.toggleMuted();
        console.log(soundContext.isMuted);
    }

    const updateTime = () => {
        const now = new Date();
        const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

        setTime(formatTime(days[now.getDay()], now.getHours(), now.getMinutes()));

        setTimeout(updateTime, 1000);
    }

    const formatTime = (day:String, hour: Number, min: Number) => {
        let parseHour = parseHourMin(hour),
        parseMin = parseHourMin(min),
        date = day + ' ' + parseHour + ':' + parseMin;

        return date;
    }

    const parseHourMin =  (num: Number) => num < 10 ? `0${num}` : num;

    return (
        <nav className="topBar">
            <div className="topBar__left">
                <h1 className="topBar__brand">Andrei Koreh</h1>
            </div>
            <div className="topBar__center"></div>
            <div className="topBar__right">
                <h4 className="topBar__email">{email}</h4>
                <div className="topBar__icons">
                    {topBarIcons.map((icon, index) => <TopBarIcon {...icon} key={index}/>)}
                </div>
                <div className="topBar__sound"></div>
                <div className="topBar__wifi"></div>
                <div className="topBar__battery"></div>
                <h4 className="topBar__time">{time}</h4>
            </div>
        </nav>
    );
}

export default TopBar;