import React from 'react';

import "./ParallaxLayer.scss";

interface Props {
    layerNumber: Number;
    layer: any;
}

const ParallaxLayer = (props: Props) => {

    
    return ( 
        <div className={`parallax__layer parallax__layer__${props.layerNumber}`}>
            <img src={props.layer} alt="parallax layer"/>
        </div>
    )
};

export default ParallaxLayer;